import React, { useState } from "react";

export const InputFieldWithStateHook = () => {
  const [counter, setCounter] = useState(0);

  return (
    <>
      <form>
        <div>
          <label htmlFor="name-input">Counter</label>
          <input type="text" name="name" id="name-input" value={counter} />
        </div>
        <div>
          <button type="button" onClick={() => setCounter(counter + 1)}>
            Increment
          </button>
        </div>
      </form>
    </>
  );
};
